/* purgecss start ignore */

:root{
  --black: #333333;
  --white: #f5f5f5;
  --backround: var(--white);
  --foreground: var(--black);
  --transition: 0.5s ease
}

html{
  background:#f5f5f5;
  background:var(--white);
  color:#333333;
  color:var(--foreground);
  transition:color 0.5s ease;
  transition:color var(--transition)
}

html::before{
  content:"";
  position:fixed;
  height:100%;
  width:100%;
  background:#333333;
  background:var(--black);
  -webkit-transform:translateX(-100%);
  transform:translateX(-100%);
  transition:-webkit-transform 0.5s ease;
  transition:transform 0.5s ease;
  transition:transform 0.5s ease, -webkit-transform 0.5s ease;
  transition:-webkit-transform var(--transition);
  transition:transform var(--transition);
  transition:transform var(--transition), -webkit-transform var(--transition);
  z-index:0
}

.dark::before{
  -webkit-transform:translateX(0);
  transform:translateX(0)
}

.hidden{
  clip:rect(0 0 0 0);
  -webkit-clip-path:inset(50%);
  clip-path:inset(50%);
  height:1px;
  overflow:hidden;
  position:absolute;
  white-space:nowrap;
  width:1px
}

.toggle-wrapper{
  width:60px;
  display:block
}

.toggle{
  height:30px;
  width:60px;
  background:#333333;
  background:var(--black);
  border-radius:15px;
  padding:4px;
  position:relative;
  margin:auto;
  cursor:pointer;
  background:#333333;
  background:var(--foreground);
  transition:background 0.5s ease;
  transition:background var(--transition)
}

.toggle::before{
  content:'';
  display:block;
  height:21px;
  width:21px;
  border-radius:10px;
  background:#f5f5f5;
  background:var(--white);
  position:absolute;
  z-index:2;
  -webkit-transform:translate(0);
  transform:translate(0);
  background:var(--background);
  transition:background 0.5s ease,-webkit-transform 0.5s ease;
  transition:transform 0.5s ease,background 0.5s ease;
  transition:transform 0.5s ease,background 0.5s ease,-webkit-transform 0.5s ease;
  transition:background var(--transition),-webkit-transform var(--transition);
  transition:transform var(--transition),background var(--transition);
  transition:transform var(--transition),background var(--transition),-webkit-transform var(--transition)
}

.toggle.enabled::before{
  -webkit-transform:translateX(32px);
  transform:translateX(32px)
}

.toggle input{
  position:absolute;
  top:0;
  opacity:0
}

.toggle .icons{
  display:flex;
  justify-content:space-between;
  align-items:center;
  height:100%;
  margin:0 px
}

.toggle .icons svg{
  height:20px;
  width:20px;
  z-index:0;
  fill:var(--background)
}

.toggle .icons svg g{
  fill:var(--background)
}

